"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import AuthCheck from "@/components/auth/AuthCheck"
import { useUser } from "@/contexts/UserContext"
import { LogOut, User } from "lucide-react"
import { Button } from "@/components/ui/button"

export function DashboardLayout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname()
  const { user, logout } = useUser()

  if (pathname === "/login") {
    return children
  }

  return (
    <AuthCheck>
      <div className="flex min-h-screen">
        {/* Sidebar */}
        <div className="w-64 bg-gray-900 text-white p-4 flex flex-col">
          <div className="mb-8">
            <h1 className="text-xl font-semibold">Usage Portal Demo</h1>
          </div>
          <nav className="flex-1">
            <ul className="space-y-2">
              <li>
                <Link
                  href="/dashboard"
                  className={cn(
                    "block px-4 py-2 rounded-lg hover:bg-gray-800",
                    pathname === "/dashboard" && "bg-gray-800"
                  )}
                >
                  Dashboard
                </Link>
              </li>
              <li>
                <Link
                  href="/usage"
                  className={cn(
                    "block px-4 py-2 rounded-lg hover:bg-gray-800",
                    pathname === "/usage" && "bg-gray-800"
                  )}
                >
                  Usage
                </Link>
              </li>
            </ul>
          </nav>
          {user && (
            <div className="border-t border-gray-800 pt-4 mt-4">
              <div className="flex items-center px-4 py-2 text-sm">
                <User className="w-4 h-4 mr-2" />
                <div>
                  <div className="font-medium">{user.name}</div>
                  <div className="text-gray-400 text-xs">{user.email}</div>
                </div>
              </div>
              <Button
                variant="ghost"
                className="w-full mt-2 text-red-400 hover:text-red-300 hover:bg-gray-800"
                onClick={logout}
              >
                <LogOut className="w-4 h-4 mr-2" />
                Logout
              </Button>
            </div>
          )}
        </div>

        {/* Main content */}
        <div className="flex-1 p-8 bg-[#f8fafc]">{children}</div>
      </div>
    </AuthCheck>
  )
}
